<template>
  <div class="pdf">
    <div class="pdf-container" ref="pdf">
      <pdf src="invoice-notification.pdf"></pdf>
    </div>

    <div
      ref="draggable"
      class="draggable"
      @touchstart="touchstart"
      @touchmove="touchmove"
      @touchend="touchend"
      :style="{
        left: (left + pos.delta.x) + 'px',
        top: (top + pos.delta.y) + 'px'
      }"
    >
      拖动我定位签章
    </div>
    <div class="position">
      <p>屏幕宽度：{{ screenWidth }}</p>
      <p>尺寸：width = {{ rect.width }}, height = {{ rect.height }} </p>
      <p>左上角位置：x = {{ Math.floor(rect.left) }}, y = {{ Math.floor(rect.top) }}</p>
      <p>中心点位置：x = {{ rect.left + rect.width >> 1 }}, y = {{ rect.top + rect.height >> 1 }}</p>
    </div>

    <p class="outside" v-if="outside">
      不要把签章放到 PDF 外面去(必须完全在浅蓝色区域内可见)
    </p>
  </div>
</template>

<script>
import Pdf from "vue-pdf";

export default {
  components: {
    Pdf
  },
  data() {
    return {
      left: 10,
      top: 10,
      rect: {},
      outside: false,
      pos: {
        start: {x: 0, y: 0},
        current: {x: 0, y: 0},
        delta: {x: 0, y: 0}
      },
      screenWidth: screen.width
    };
  },
  mounted() {
    this.rect = this.$refs.draggable.getBoundingClientRect()
  },
  methods: {
    touchstart(e) {
      e.preventDefault()
      e.stopPropagation()

      // let target = e.target
      // 定义开始移动位置
      this.setPos(e, this.pos.start)
    },
    touchmove(e) {
      this.setPos(e, this.pos.current)
      // 计算目标元素需移动的距离
      this.pos.delta.x = this.pos.current.x - this.pos.start.x
      this.pos.delta.y = this.pos.current.y - this.pos.start.y
    },
    touchend() {
      // 重置数值，方便下一次拖拽
      this.left += this.pos.delta.x
      this.top += this.pos.delta.y
      this.pos.delta.x = 0
      this.pos.delta.y = 0

      this.rect = this.$refs.draggable.getBoundingClientRect()
      this.testIfOutside()
    },
    setPos(e, target) {
      target.x = e.pageX || e.touches[0].pageX
      target.y = e.pageY || e.touches[0].pageY
    },
    testIfOutside() {
      let rectPdf = this.$refs.pdf.getBoundingClientRect()
      this.outside = false
      if (this.rect.left < rectPdf.left
        || this.rect.right > rectPdf.right
        || this.rect.top < rectPdf.top
        || this.rect.bottom > rectPdf.bottom) {
        this.outside = true
      }
    }
  }
};
</script>

<style>
.pdf-container {
  background: aliceblue;
  padding: 10px;
}
.draggable {
  position: absolute;
  left: 10px;
  top: 10px;
  background: royalblue;
  color: #fff;
  white-space: nowrap;
}
.position {
  font-size: 12px;
  text-align: left;
  padding: 5px;
}
.outside {
  color: red;
}
</style>
