<template>
  <div class="about" @drop="drop" @dragover="dragover">
    <div class="pdf-container" ref="pdf">
      <pdf src="invoice-notification.pdf"></pdf>
    </div>

    <div
      ref="draggable"
      class="draggable"
      draggable="true"
      @dragstart="dragstart"
      @drag="drag"
      @dragend="dragend"
      :class="{ dragging: dragging }"
      :style="{
        left: left + 'px',
        top: top + 'px'
      }"
    >
      拖动我定位签章
    </div>
    <div class="position">
      <p>尺寸：width = {{ rect.width }}, height = {{ rect.height }} </p>
      <p>左上角位置：x = {{ rect.left }}, y = {{ rect.top }}</p>
      <p>中心点位置：x = {{ rect.left + rect.width >> 1 }}, y = {{ rect.top + rect.height >> 1 }}</p>
    </div>

    <p v-if="outside">
      不要把签章放到 PDF 外面去
    </p>
  </div>
</template>

<script>
import Pdf from "vue-pdf";

export default {
  components: {
    Pdf
  },
  data() {
    return {
      left: 10,
      top: 10,
      dragging: false,
      offsetX: 0,
      offsetY: 0,
      rect: {},
      outside: false
    };
  },
  methods: {
    onDrop(dropResult) {
      console.log(dropResult);
      // this.items = applyDrag(this.items, dropResult);
    },
    dragstart(event) {
      console.log("dragstart", event.offsetX, event.offsetY);
      this.offsetX = event.offsetX;
      this.offsetY = event.offsetY;
      setTimeout(() => {
        this.dragging = true;
      }, 1);
      event.dataTransfer.effectAllowed = "move";
    },
    drag() {
      // console.log("drag", event);
    },
    dragend(event) {
      console.log("dragend", event.offsetX, event.offsetY);
      this.left = event.pageX - this.offsetX;
      this.top = event.pageY - this.offsetY;
      this.dragging = false;
    },
    // dragenter和dragover事件的默认行为是拒绝接受任何被拖放的元素。
    // 因此，我们必须阻止浏览器这种默认行为。
    dragover(event) {
      console.log("dragover", event);
      if (event.preventDefault) {
        event.preventDefault(); // Necessary. Allows us to drop.
      }

      // 【none、move、copy、link】
      event.dataTransfer.dropEffect = "move"; // See the section on the DataTransfer object.

      return false;
    },
    // 当被拖动元素在目的地元素里放下时触发，一般需要取消浏览器的默认行为。
    drop(event) {
      console.log("drop", event);
      // this / e.target is current target element.

      /*
      this.$nextTick(() => {
        this.rect = this.$refs.draggable.getBoundingClientRect()
      })
      */
      setTimeout(() => {
        this.rect = this.$refs.draggable.getBoundingClientRect()
        let rectPdf = this.$refs.pdf.getBoundingClientRect();

        this.outside = false
        if (this.rect.left < rectPdf.left
          || this.rect.right > rectPdf.right
          || this.rect.top < rectPdf.top
          || this.rect.bottom > rectPdf.bottom) {
          this.outside = true
        }

      }, 100)

      if (event.stopPropagation) {
        event.stopPropagation(); // stops the browser from redirecting.
      }

      // See the section on the DataTransfer object.

      return false;
    }
  }
};
</script>

<style>
.about {
  background: aliceblue;
}
.draggable {
  position: absolute;
  left: 10px;
  top: 10px;
  background: royalblue;
  color: #fff;
}
.dragging {
  transform: translateX(-9999px);
}
[draggable] {
  -moz-user-select: none;
  -khtml-user-select: none;
  -webkit-user-select: none;
  user-select: none;
  /* Required to make elements draggable in old WebKit */
  -khtml-user-drag: element;
  -webkit-user-drag: element;
}
</style>
